import React, { Component } from "react";
import CV from "../../Resume_DebanjanGanguly.pdf";

class DownloadCV extends Component {
  state = {
    resumeLink: "Resume_DebanjanGanguly.pdf"
  };
  styleDiv = {
    width: "25%",
    marginLeft: "auto",
    marginRight: "auto",
    marginTop: "25px",
    position: "relative"
  };
  render() {
    return (
      <div id="button_area" style={this.styleDiv}>
        <a id="link-resume" href={CV} download="resumeDebanjan.pdf">
          Resume
        </a>
        <a id="link-about" href="#about">
          View My Work
        </a>
      </div>
    );
  }
}

export default DownloadCV;



